 * @Author: wangxi
 * @Date: 2019-11-29 17:38:14
 * @LastEditors: wangxi
 * @LastEditTime: 2019-12-06 15:44:26
 -->
<template>
    <div id="chart-box" class="chart-box"   ref="imageTofile">
        <div
            :class="{'attackEffect' : (currentIndex >= 40 && currentIndex <= 49),attackEffects : (currentIndex >= 50 && currentIndex <= 59)}"
        ></div>
        <!-- <div class="attackEffect"></div> -->

        <div class="chart-wrapper">
            <v-chart
                style="width: 100%;height: 100%"
                ref="chart"
                :autoresize="true"
                id="chart"
                :options="option"
            />
        </div>
        <!--  -->
        <div
            v-if="chartLoadingStatus"
            :style="{top:0,left:startY + 'px',transform:' translateY('+ startx +'px)'}"
            class="position-a circle"
        >
            <div class="pulse"></div>
            <div class="pulse1"></div>
        </div>
        <!--  -->
        <div
            v-if="chartLoadingStatus && sceenWidth > 768"
            :style="{left:startY + 10 + 'px', borderColor: '#494869',borderWidth: '1px',position: 'absolute',top:startx + 4 +'px',borderStyle: 'dashed',height: 0, width: lastpointValue - startY + 'px'}"
        ></div>
        <!--  -->
        <div
            :style="{top: startx ? startx - 7 +'px' : '-300px', right: 30 + 'px', display: sceenWidth > 768 ? 'block' : 'none'}"
            class="position-a endBorder"
        >
            <div>{{closePrice}}</div>
        </div>
        <div class="audio">
            
        </div>
    </div>
</template>

<script>
import { mapState } from "vuex";
import { Event } from '@/utils/eventBus'
import ECharts from "vue-echarts";
import "echarts/lib/chart/line";
import "echarts/lib/component/markArea";
import "echarts/lib/component/markLine";
import "echarts/lib/component/markPoint";
import "echarts/lib/component/tooltip"; //
import SockJS from "sockjs-client";
import Stomp from "stompjs";
import html2canvas from 'html2canvas'
export default {
    name: "BussinessChart",
    components: {
        "v-chart": ECharts,
        html2canvas
    },
    data() {
        return {
            option: {
                animation: true,
                animationDuration: 1500,
                tooltip: {
                    trigger: "axis",
                    formatter: function(params) {
                        return "";
                    }
                },
                xAxis: {
                    type: "time",
                    boundaryGap: false,
                    splitLine: {
                        show: false
                    },
                    minInterval: 1,
                    axisLine: {
                        lineStyle: {
                            color: "#333",
                            width: 1
                        }
                    },
                    axisLabel: {
                        textStyle: {
                            color: "#a0a0c1"
                        }
                    },
                    splitNumber: 20,
                    splitArea: false //x
                },
                yAxis: {
                    type: "value",
                    splitLine: {
                        show: false
                    },
                    axisLine: {
                        lineStyle: {
                            color: "#333",
                            width: 1
                        }
                    },
                    axisLabel: {
                        formatter: function(value, index) {
                            return value.toFixed(2);
                        },
                        textStyle: {
                            color: "#a0a0c1"
                        }
                    },
                    min: function(value) {
                        return Math.floor(value.min - 1);
                    },
                    max: function(value) {
                        return Math.floor(value.max + 1);
                    }
                },
                series: [
                    {
                        data: [],
                        type: "line",
                        symbol: 'none',
                        // symbol: 'image://data:image/png;base64,',
                        // symbolSize: 100,
                        itemStyle: {
                            color: 'rgba(255,255,255, .8)'
                        },
                        //
                        lineStyle: {
                            normal: {
                                color: "#ddd" // 
                            }
                        },
                        markLine: {
                            silent: true,
                            symbol: "none",
                            lineStyle: {
                                type: "solid",
                                width: 1
                            },
                            itemStyle: [
                                {
                                    color: {
                                        type: "linear",
                                        x: 0,
                                        y: 0,
                                        x2: 0,
                                        y2: 1,
                                        global: false //  false
                                    }
                                }
                            ],
                            data: [
                                {
                                    lineStyle: {
                                        color: "red"
                                    },
                                    xAxis: 1573467302915
                                },
                                {
                                    lineStyle: {
                                        color: "pink"
                                    },
                                    xAxis: 1573467305414
                                }
                            ]
                        },
                        areaStyle: {
                            normal: {
                                color: {
                                    type: "linear",
                                    x: 0,
                                    y: 0,
                                    x2: 0,
                                    y2: 1,
                                    colorStops: [
                                        {
                                            offset: 0,
                                            color: "#DDDDDD" // 0% 
                                        },
                                        {
                                            offset: 1,
                                            color: "rgba(0,0,0,0)" // 100% 
                                        }
                                    ],
                                    global: false //  false
                                },
                                shadowColor: "rgba(0, 0, 0, 0.1)",
                                shadowBlur: 10
                            }
                        }
                    }
                ]
            },
            bar: null, //
            timeArr: [], //x
            showData: [], //y
            len: 60, //60x
            currentIndex: new Date().getSeconds(), //
            startx: 0,
            startY: 0,
            activeNum: 0, //
            isEndBuyStatus: false, //
            lastTenpointValue: 1000, //
            lastpointValue: -999,
            chartLoadingStatus: false, //
            needEmptyStatus: false, //
            timer: null,
            websock: null,
            SuspensionEndTime: "",
            websockData: [
                {
                    closePrice: 0
                }
            ],
            isNeedInitStatus: true, //
            splitNumber: 20, //x
            math: 1450, //
            stompClient: null, //sock
            websockTimer: null, // websockettimer
            actionTimer: null,
            initTimeStamp: 0, // 
            lastTenData: [], //
            lastSecondData: [], //
            getBeforeEnterList: [] //
        };
    },
    watch:{
        currentIndex:function(){
            let box = document.getElementById('chart-box');
            if(this.currentIndex == 39){
                var iframe = document.createElement('iframe');
                iframe.width = '0';
                iframe.allow = 'autoplay';
                iframe.className = 'iframe';
                iframe.src = 'http://pic.ibaotu.com/01/00/54/97i888piC624.mp3';
                box.appendChild(iframe)
            }
            if(this.currentIndex == 50){
                let iframes = document.getElementsByClassName('iframe')[0]
                if(iframes){
                    box.removeChild(iframes)
                }
            }
            if(this.currentIndex == 60){
                this.toImage() ;
            }
        }
    },
    computed: {
        ...mapState({
            currentBuyInfo: state => state.currentBuyInfo,
            sceenWidth: state => state.sceenWidth,
            currentIconInfo: state => state.currentIconInfo
        }),
        closePrice() {
            return this.websockData.closePrice || 0;
        }
    },
    created() {},
    mounted() {
        // this.initWebSocket();
        this.initSocketJs()
        this.bar = this.$refs.chart.chart;
        // x
        const sceenWidth = document.body.clientWidth;
        this.resetSplitNumber(sceenWidth);
        window.addEventListener("resize", e => {
            this.resetSplitNumber(e.target.innerWidth);
        });
        this.howLongAction(30);
        // 
        // this.testData()
        // 
        Event.$on('switchIcon', () => {
            this.disconnect()
            this.isNeedInitStatus = true;
            this.initSocketJs()
            // loading
            const loading = this.$loading({
                lock: true,
                text: '',
                spinner: 'el-icon-loading',
                background: 'rgba(0, 0, 0, 0.7)'
            });

            setTimeout(() => {
                loading.close();
            }, 500);
        })
    },
    destroyed() {
        this.disconnect() //websocket
    },
    methods: {
        // 
        toImage() {
            // ,,
            html2canvas(this.$refs.imageTofile, {
                backgroundColor: null,
                useCORS: true // ,,,
            }).then(canvas => {
                let url = canvas.toDataURL("image/png");
                // this.htmlUrl = url;
                this.$store.state.htmlUrl = url;
                // base64,
                // this.sendUrl();
            });
        },

        // 
        sendUrl() {
            // formData,,
            const formData = new FormData();
            formData.append("base64", this.company.fileUrl);
            formData.append("userId", 123);
            formData.append("pathName", "pdf");
            this.$ajax({
                url: apiPath.common.uploadBase,
                method: "post",
                data: formData
            }).then(res => {
                if (res.code && res.data) {
                }
            });
        },
        // ,
        howLongAction(time = 2) {
            var userTime = time * 60;
            var objTime = {
                init: 0,
                time: () => {
                    objTime.init += 1;
                    if (objTime.init === userTime) {
                        this.disconnect()
                        clearInterval(this.actionTimer);
                        this.$confirm(
                            "",
                            "",
                            {
                                confirmButtonText: "",
                                type: "warning",
                                showClose: false,
                                showCancelButton: false,
                                closeOnClickModal: false
                            }
                        ).then(() => {
                            location.reload()
                        })
                    }
                },
                eventFun: function() {
                    clearInterval(this.actionTimer);
                    objTime.init = 0;
                    this.actionTimer = setInterval(objTime.time, 1000);
                }
            };

            this.actionTimer = setInterval(objTime.time, 1000);

            var body = document.querySelector("html");
            body.addEventListener("click", objTime.eventFun);
            body.addEventListener("keydown", objTime.eventFun);
            body.addEventListener("mousemove", objTime.eventFun);
            body.addEventListener("mousewheel", objTime.eventFun);
        },
        closeTion(type) {
            var audio = document.getElementById("music1");
            if(type){
                audio.play(); // 
            }else{
                audio.pause(); // 
            }
            
        },
        resetSplitNumber(width) {
            if (width <= 1440) {
                this.splitNumber = 10;
            }
            if (width <= 768) {
                this.splitNumber = 5;
            }
        },
        testData() {
            this.testTimer = setInterval(() => {
                this.math =
                    this.math +
                    Math.random() * Math.random() * 0.02 -
                    Math.random() * Math.random() * 0.02;
                var obj = {
                    code: "XAUUSD",
                    market: "fx",
                    period: "new",
                    price: this.math,
                    signals: "dfcf_fx_XAUUSD_001",
                    time: this.$date.formatDateTime(new Date()),
                    volume: "2.931519/0.002015"
                };
                this.handleChartWebsocketData(JSON.stringify(obj));
            }, 1000);
        },
        initSocketJs() {
            // 
            let query = {
                market: this.currentIconInfo.market,
                code: this.currentIconInfo.code
            }
            this.$http.getBeforeEnterData(query).then((res) => {
                if(res){
                    // [timestemp, price]
                    this.getBeforeEnterList = res.data.data.map(v => {
                        return [new Date(v.createdTime).getTime(), v.price]
                    })
                }
                this.connection();
                let that= this;
                // ,,
                this.websockTimer = setInterval(() => {
                    try {
                        that.stompClient.send("test");
                    } catch (err) {
                        console.log(": " + err);
                        that.connection();
                    }
                }, 5000);
            })
        },
        connection() {
            // 
            let socket = new SockJS('http://192.168.101.13:4000/webSocketEndPoint/');
            // STOMP
            this.stompClient = Stomp.over(socket);
            let header = {
                token: '212131'
            }
            this.stompClient.debug = null
            this.stompClient.connect(header, (data) => {
                this.stompClient.subscribe(`/pic/${this.currentIconInfo.market}-${this.currentIconInfo.code}`, (response) => {
                    this.handleChartWebsocketData(response.body);
                })

                this.stompClient.send("/app/pr",
                    {},
                  JSON.stringify({'message': '123'}),
                )
            }, (err) => {
                // 
                console.log('')
                console.log(err);
            });
        },
        disconnect() {
            if (this.stompClient) {
                this.stompClient.disconnect();
            }
            if(this.websock){
                this.websock.close()
            }
            if(this.testTimer){
                clearInterval(this.testTimer)
            }
            if(this.websockTimer){
                clearInterval(this.websockTimer)
            }
        },
        initWebSocket() {
            //weosocket
            const wsuri = "ws://192.168.101.13:4001/websocket/1321231";
            this.websock = new WebSocket(wsuri);
            this.websock.onmessage = this.websocketonmessage;
            this.websock.onopen = this.websocketonopen;
            this.websock.onerror = this.websocketonerror;
            this.websock.onclose = this.websocketclose;
        },
        websocketonopen() {
            //send
        },
        websocketonerror() {
            //
            this.initWebSocket();
        },
        websocketonmessage(e) {
            this.handleChartWebsocketData(e.data);
        },
        websocketsend(Data) {
            //
            this.websock.send(Data);
        },
        websocketclose(e) {
            //
            console.log("", e);
        },
        // 
        handleChartWebsocketData(chartData) {
            const redata = JSON.parse(chartData);
            redata.showCode = redata.code.replace(/^USD/, '$&\/').replace(/USD$/, '\/$&')
            this.$store.commit("setChartData", redata);
            // 
            let serverTime = new Date(redata.time).getTime();
            // 
            this.websockData = {
                server_time: serverTime,
                closePrice: redata.price
            }
            // 01
            if (this.initTimeStamp && serverTime - this.initTimeStamp > (this.len + 1) * 1000) {
                // 
                this.$emit("endTrade", this.initTimeStamp);
                this.toImage()
                // true
                this.isNeedInitStatus = true;
            }

            // 
            if (this.initTimeStamp && serverTime - this.initTimeStamp > (this.len - 10) * 1000 && serverTime - this.initTimeStamp <= this.len * 1000) {
                this.lastTenData.push([serverTime, this.websockData.closePrice])
            }

            // 0001
            if (this.initTimeStamp && serverTime - this.initTimeStamp > this.len * 1000 && serverTime - this.initTimeStamp < (this.len + 1) * 1000) {
                this.lastSecondData.push([serverTime, this.websockData.closePrice])
                // 00
                let lastCutoffData = this.showData[this.showData.length - 1]
                const millSecond = serverTime.toString().substr(-3, 3)
                lastCutoffData[0] = serverTime - millSecond
                // 6060
                this.websockData = {
                    server_time: lastCutoffData[0],
                    closePrice: lastCutoffData[1]
                }
            }

            // 
            this.currentIndex = this.formatDate(
                2,
                serverTime
            );
            if (this.isNeedInitStatus) {
                // 000
                const millSecond = this.websockData.server_time.toString().substr(-3, 3)
                this.initTimeStamp =
                    this.websockData.server_time - (this.currentIndex) * 1000 - millSecond;
                this.chartLoadingStatus = false;
                this.init();
                this.chartLoadingStatus = true;
                this.isNeedInitStatus = false;
            }

            // 
            this.lastpointValue = document.getElementsByClassName(
                "endBorder"
            )[0].offsetLeft;

            this.refreshData(this.websockData);
        },
        // 
        init() {
            this.showData = [];
            // 71
            this.showData = new Array(this.len + 1).fill(0);
            // 5050
            // 
            // showdata
            if(this.lastTenData.length){
                this.lastTenData[0][0] = this.initTimeStamp
            }
            // 000
            if(this.lastSecondData.length){
                this.lastSecondData[0][0] = this.initTimeStamp
            }   
            // 000
            if(this.getBeforeEnterList.length){
                this.getBeforeEnterList[0][0] = this.initTimeStamp
            }
            this.showData.forEach((v, i) => {
                this.showData[i] = [this.initTimeStamp + i * 1000, ""];
            });
            // this.showData = [...this.showData, ...this.lastTenData]
            // ,
            this.showData = [...this.showData, ...this.lastSecondData, ...this.getBeforeEnterList]
            // 
            this.lastTenData = []
            this.lastSecondData = []
            this.getBeforeEnterList = []
            this.SuspensionEndTime = this.showData[this.len][0];
            this.$store.dispatch(
                "setSuspensionEndTime",
                this.SuspensionEndTime
            );
            this.draw();
        },
        // 
        formatDate(type, timeStamp) {
            let now = new Date(timeStamp);
            var hour = now.getHours(); //023
            var minute = now.getMinutes(); //059
            var seconds = now.getSeconds(); //059
            if (type === 0) {
                return hour;
            } else if (type === 1) {
                return minute;
            } else if (type === 2) {
                return seconds;
            }
        },
        refreshData(websockData) {
            this.showData.push([websockData.server_time, websockData.closePrice]);
            this.draw();
        },
        draw() {
            let obj = JSON.parse(JSON.stringify(this.option));
            // 
            obj.yAxis.min = function(value) {
                return value.min - 0.02;
            };
            obj.yAxis.max = function(value) {
                return value.max + 0.02;
            };
            // y2
            obj.yAxis.axisLabel = {
                formatter: function(value, index) {
                    return value.toFixed(2);
                },
                textStyle: {
                    color: "#a0a0c1"
                }
            };
            // x
            obj.xAxis.splitNumber = this.splitNumber;

            // 
            let arr = [
                // {
                //     lineStyle: {
                //         color: {
                //             type: "linear",
                //             x: 0,
                //             y: 0,
                //             x2: 0,
                //             y2: 1,
                //             colorStops: [
                //                 {
                //                     offset: 0,
                //                     color: "#1b1b2e" // 0% 
                //                 },
                //                 {
                //                     offset: 1,
                //                     color: "#04affe" // 100% 
                //                 }
                //             ]
                //         }
                //     },
                //     label: {
                //         show: true,
                //         normal: {
                //             show: true,
                //             textStyle: {
                //                 fontSize: 14,
                //                 color: "#04affe"
                //             },
                //             formatter: function(params) { 
                //                 return `${params.value.split(" ")[1]}\n`;
                //             }
                //         }
                //     },
                //     xAxis: this.showData[10]
                //         ? this.$date.formatDateTime(
                //               this.showData[10][0]
                //           )
                //         : ""
                // },
                {
                    lineStyle: {
                        color: {
                            type: "linear",
                            x: 0,
                            y: 0,
                            x2: 0,
                            y2: 1,
                            colorStops: [
                                {
                                    offset: 0,
                                    color: "#1b1b2e" // 0% 
                                },
                                {
                                    offset: 1,
                                    color: "#439957" // 100% 
                                }
                            ]
                        }
                    },
                    label: {
                        show: true,
                        normal: {
                            show: true,
                            textStyle: {
                                fontSize: 14,
                                color: "#439957"
                            },
                            formatter: function(params) {
                                return params.value.split(" ")[1];
                            }
                        }
                    },
                    xAxis: this.showData[this.len - 10]
                        ? this.$date.formatDateTime(
                              this.showData[this.len - 10][0]
                          )
                        : ""
                },
                {
                    lineStyle: {
                        color: {
                            type: "linear",
                            x: 0,
                            y: 0,
                            x2: 0,
                            y2: 1,
                            colorStops: [
                                {
                                    offset: 0,
                                    color: "#1b1b2e" // 0% 
                                },
                                {
                                    offset: 1,
                                    color: "#ca2626" // 100% 
                                }
                            ]
                        }
                    },
                    label: {
                        show: true,
                        normal: {
                            show: true,
                            textStyle: {
                                fontSize: 14,
                                color: "#ca2626"
                            },
                            formatter: function(params) {
                                return params.value.split(" ")[1];
                            }
                        }
                    },
                    xAxis: this.showData[this.len]
                        ? this.$date.formatDateTime(this.showData[this.len][0])
                        : ""
                }
            ];

            // 
            obj.tooltip = {
                trigger: "axis",
                formatter: function(params) {
                    // 
                    let data = "";
                    params.forEach(v => {
                        if (v.data[1]) {
                            data = v.data[1];
                        }
                    });
                    const time = params[0].axisValueLabel;

                    if (data) {
                        return `<p>${time}<br>price${data}</p>`;
                    } else {
                        return "";
                    }
                }
            };

            // 
            const buyInfoCookie = this.$common.getCookie("currentBuyInfo");
            let cacheOrderList = [];
            if (buyInfoCookie && buyInfoCookie !== "null") {
                cacheOrderList = JSON.parse(buyInfoCookie);
            }
            if (this.currentBuyInfo.length === 0 && cacheOrderList.length > 0) {
                // 
                this.$common.setCookie(
                    "currentBuyInfo",
                    JSON.stringify(cacheOrderList)
                );
                this.$store.commit("setCurrentBuyInfo", cacheOrderList);
            } else {
                // vuex
                if (this.currentBuyInfo.length > 50) {
                    // 5050
                    let sliceRecord = this.currentBuyInfo.slice(-50);
                    this.$common.setCookie(
                        "currentBuyInfo",
                        JSON.stringify(sliceRecord)
                    );
                } else {
                    this.$common.setCookie(
                        "currentBuyInfo",
                        JSON.stringify(this.currentBuyInfo)
                    );
                }
            }

            obj.series[0].markPoint = {
                symbolSize: "40",
                label: {
                    normal: {
                        formatter: function(param) {
                            return param != null ? Math.round(param.value) : "";
                        }
                    }
                },
                data: this.currentBuyInfo,
                tooltip: {
                    formatter: function(param) {
                        return param.name + "<br>" + (param.data.coord || "");
                    }
                }
            };
            // 
            obj.series[0].animationEasing = function(k) {
                return k;
            };

            obj.series[0].animationDuration = 2000;
            obj.series[0].markLine.data = arr;
            obj.series[0].data = this.showData;

            this.option = obj;
            this.onClick();
        },
        onClick() {
            setTimeout(() => {
                let obj = JSON.parse(JSON.stringify(this.option)).series[0]
                    .data;
                let arr = this.bar.getViewOfSeriesModel(
                    this.bar.getModel().getSeries()[0]
                )._points;
                if (arr.length >= 62) {
                    let lastOne = arr[arr.length - 1];
                    this.startY = lastOne[0] - 5;
                    this.startx = lastOne[1] - 5;
                    this.activeNum = this.showData[
                        this.currentIndex + this.len
                    ];
                }
            }, 30);
        }
    }
};
</script>

<style lang='scss' scoped>
.attackEffect {
    //
    position: absolute;
    width: 100%;
    height: 100%;
    border: 1px solid red;
    box-sizing: border-box;
    box-shadow: inset 0px 0px 40px red;
    animation: mymove 2s infinite;
    @keyframes mymove {
        0% {
            opacity: 0;
        }
        50% {
            opacity: 1;
        }
        100% {
            opacity: 0;
        }
    }
}
.attackEffects {
    position: absolute;
    width: 100%;
    height: 100%;
    border: 1px solid red;
    box-sizing: border-box;
    box-shadow: inset 0px 0px 40px red;
}
.chart-box {
    width: 100%;
    height: 540px;
    position: relative;
    // background: url("../assets/bj.png") no-repeat 100% 100%;
    background: #1b1b2e;
    .chart-wrapper {
        width: 100%;
        height: 100%;
        position: relative;
    }
}
.circle {
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background: #dddddd;
    // transition: all 0.3s;
}
.position-a {
    position: absolute;
}
.startBorder,
